.container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 auto;
  max-width: 96rem;
  width: 100%;
}

.auto {
  height: auto;
  margin-top: 2rem;
  padding: 2rem;
}

.title {
  font-size: 8rem;
  text-transform: uppercase;
}

@media screen and (max-width: 960px) {
  .container {
    height: auto;
    margin-top: 2rem;
    padding: 2rem;
  }

  .title {
    font-size: 4rem;
  }
}



