@-webkit-keyframes timerColors {
  0% {
    color: var(--color-aquamarine);
  }

  33% {
    color: var(--color-goldenrod);
  }

  66% {
    color: var(--color-froly);
  }
}

@keyframes timerColors {
  0% {
    color: var(--color-aquamarine);
  }

  33% {
    color: var(--color-goldenrod);
  }

  66% {
    color: var(--color-froly);
  }
}

@-webkit-keyframes circleOffset {
  to {
    stroke-dashoffset: -600;
  }
}

@keyframes circleOffset {
  to {
    stroke-dashoffset: -600;
  }
}

.countdown-wrapper {
  -webkit-animation: timerColors 60s infinite step-end;
          animation: timerColors 60s infinite step-end;
  margin-top: 5rem;
  height: 10rem;
  position: relative;
  width: 10rem;
}

.countdown-time {
  color: currentColor;
  font-size: 4rem;
  line-height: 10rem;
  position: absolute;
  text-align: center;
  width: 100%;
}

.countdown-circle {
  -webkit-animation: circleOffset 1s infinite forwards;
          animation: circleOffset 1s infinite forwards;
  fill: none;
  stroke: currentColor;
  stroke-dasharray: 600;
  stroke-linecap: round;
  stroke-width: .8rem;
  stroke-dashoffset: 600;
}



